function debounce (func, wait) {
  let timeout
  return function (...args) {
    clearTimeout(timeout)
    timeout = setTimeout(() => func.apply(this, args), wait)
  }
}
document.addEventListener('DOMContentLoaded', () => {
  const menuBtnan = document.querySelector('.checkbox-label')
  const items = document.querySelector('.nav-items')
  const searchbox = document.querySelector('.search-box')
  const searchbox1 = document.querySelector('.search-box1')
  const searchboxicons = document.querySelectorAll('.search-box-icon')
  let currentElement = null// 当前记录的点击元素
  let pmwidth = window.innerWidth
  var a = 0
  // 更新滑块位置
  function updateSearchBoxPosition () {
    if (pmwidth <= 990) {
      if (searchbox1 && currentElement) {
        searchbox1.style.top = currentElement.offsetTop + 'px'
        searchbox1.style.left = ''
      }
    } else {
      if (searchbox && currentElement) {
        searchbox.style.left = currentElement.offsetLeft + 'px'
        searchbox.style.width = currentElement.offsetWidth + 'px'
        searchbox.style.top = ''// 隐藏 top 属性
      }
    }
  }

  // 初始化位置
  window.onload = updateSearchBoxPosition

  // 菜单点击事件
  if (menuBtnan && items) {
    menuBtnan.addEventListener('click', () => {
      items.classList.toggle('active')
      menuBtnan.classList.toggle('hide')
    })
  }

  // 点击搜索图标更新 currentElement 并调整滑块位置
  if (searchboxicons.length > 0) {
    searchboxicons.forEach((icon) => {
      icon.addEventListener('click', () => {
        currentElement = icon// 记录当前点击的元素
        pmwidth = window.innerWidth// 更新当前窗口宽度
        updateSearchBoxPosition()// 更新滑块位置
        menuBtnan.click()// 关闭菜单
      })
    })
  }

  // 处理窗口大小变化
  window.addEventListener('resize', debounce(() => {
    pmwidth = window.innerWidth
    updateSearchBoxPosition()// 窗口大小变化时更新滑块位置
  }), 300)
  // 判断鼠标滚轮方向
  window.addEventListener('mousewheel', function (event) {
    if (event.wheelDelta > 0) {
      if (a > 0) {
        a--
      }
    } else {
      // 向下滚动
      if (a < searchboxicons.length - 1) {
        a++
      }
      console.log('向下滚动')
    }
    // 创建点击事件
    const clickEvent = new MouseEvent('click', {
      bubbles: true, // 确保事件可以冒泡
      cancelable: true // 确保事件可以被取消
    })
    console.log(event.wheelDelta + ' 向上滚动')
    menuBtnan.click()// 关闭菜单
    // 获取 <li> 标签内的 <a> 标签并触发点击事件
    const link = searchboxicons[a].querySelector('a')
    if (link) {
      link.dispatchEvent(clickEvent)
    } else {
      console.log('No <a> tag found inside the <li>')
    }
  })
})
